import React from 'react';
import {
  RecipeList_recipes$key,
  RecipeList_recipes$data,
} from '../../__generated__/relay/RecipeList_recipes.graphql';
import styled from 'styled-components';
import {
  createPaginationContainer,
  RelayPaginationProp,
  graphql,
} from 'react-relay';
import RecipeItem from './RecipeItem';
import { useFragment, usePaginationFragment } from 'relay-hooks';

interface Props {
  recipes: RecipeList_recipes$key;
}

const ItemContainer = styled.div`
  padding: 8px;
  border: 1px solid grey;
  border-radius: 4px;
  margin-bottom: 12px;
  background-color: ${({ theme }) => theme.colors.light.light};
`;

const fragmentSpec = graphql`
  fragment RecipeList_recipes on Query
  @refetchable(queryName: "RecipeQuery")
  @argumentDefinitions(
    first: { type: "Int", defaultValue: 3 }
    after: { type: "Cursor" }
  ) {
    allRecipes: allRecipes(first: $first, after: $after)
      @connection(key: "RecipeList_allRecipes") {
      edges {
        node {
          id
          ...RecipeItem_recipe
        }
      }
    }
  }
`;

export default function RecipeList({ recipes }: Props): JSX.Element {
  const { data, loadNext, hasNext } = usePaginationFragment(
    fragmentSpec,
    recipes,
  );

  const noItem = "Couldn't load item";
  if (data?.allRecipes?.edges != null) {
    const recipeItems = data.allRecipes.edges.map(({ node }, index) => {
      if (node == null) {
        return <p>{noItem}</p>;
      }
      return (
        <ItemContainer key={node.id}>
          {index}. <RecipeItem recipe={node} />
        </ItemContainer>
      );
    });
    return (
      <>
        {recipeItems}
        <button disabled={!hasNext} onClick={() => loadNext(5)}>
          Load more
        </button>
      </>
    );
  } else {
    return <div>Error</div>;
  }
}

